<template>
  <div ref="layout" class="layout" :style="layout_tips_style">
    <div ref="reader" v-if="chapter!=null" class="reader">
      <div class="chaptertitle" :style="title_tips_style">{{chapter.title}}</div>
      <div class="chaptercontent" :style="chapter_tips_style">{{chapter.cpContent}}</div>
      <div class="chapterfooter">本章到此结束</div>
    </div>
    <div v-if="showmenu" class="topmenu">
      <div class="back" @click.stop="back">返回</div>
      <img class="chaptericon" src="../../imgs/chapters.png" @click="togglechapters" />
    </div>
    <div v-if="showmenu" class="footermune">
      <div class="bgcolor">
        背景：
        <b
          v-for="(color,index) in colorlist"
          :key="index"
          :style="{backgroundColor:color}"
          @click.stop="changebgcolor(color)"
        ></b>
      </div>
      <div class="fontsize">
        大小：
        <b
          v-for="(size,index) in sizelist"
          :key="index"
          :style="{fontSize:size}"
          @click.stop="changefontsize(size)"
        >{{size}}</b>
      </div>
    </div>
    <div v-if="showchapters" class="chapterlist">
      <div
        class="chapteritem"
        v-for="(item,index) in chapters"
        :key="index"
        @click="changechapter(index)"
      >{{item.title}}</div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  props: {
    bookid: String,
    source: String,
    chapterindex: Number
  },
  data () {
    return {
      chapters: [],
      chapter: null,
      showmenu: false,
      showchapters: false,
      curentchapterindex: 0,
      startX: 0,
      startY: 0,
      colorlist: ['#fff', '	#faebd7', '#8fbc8f', '#000'],
      sizelist: ['14px', '16px', '18px', '20px'],
      layout_tips_style: {},
      chapter_tips_style: {},
      title_tips_style: {}
    }
  },
  watch: {
    chapter: function () {
      if (this.$refs.reader) {
        this.$refs.reader.scrollTo(0, 0);
      }
    }
  },
  methods: {
    changechapter (chapterindex) {
      if (chapterindex >= 0 && chapterindex < this.chapters.length) {
        axios.get('http://novel.kele8.cn/chapters/' + encodeURIComponent(this.chapters[chapterindex].link)).then((res) => {
          this.chapter = res.data.chapter;
          this.curentchapterindex = chapterindex;
          this.showmenu = false;
          this.showchapters = false;
        });
      }
    },
    getchapters () {
      axios.get('http://novel.kele8.cn/book-chapters/' + this.source).then(((res1) => {
        // console.log(res1);
        this.chapters = res1.data.chapters;
        axios.get('http://novel.kele8.cn/chapters/' + encodeURIComponent(res1.data.chapters[this.chapterindex].link)).then((res2) => {
          // console.log(res2);
          this.chapter = res2.data.chapter;
        });
      }));
    },
    togglemenu () {
      this.showmenu = !this.showmenu;
    },
    togglechapters () {
      if (!this.showchapters) {
        this.getchapters();
      }
      this.showchapters = !this.showchapters;
    },
    back () {
      this.$router.back();
    },
    changebgcolor (color) {
      this.layout_tips_style.backgroundColor = color;
      if (color === '#000') {
        this.chapter_tips_style.color = "#eee";
        this.title_tips_style.color = "#eee";
      } else {
        this.chapter_tips_style.color = "#333";
        this.title_tips_style.color = "#333";
      }
    },
    changefontsize (size) {
      this.chapter_tips_style.fontSize = size;
    },
    handleTouchStart (event) {
      this.startX = event.touches[0].clientX;
      this.startY = event.touches[0].clientY;
    },
    handleTouchEnd (event) {
      const x = event.changedTouches[0].clientX;
      const y = event.changedTouches[0].clientY;
      if (Math.abs(x - this.startX) < 5 && Math.abs(y - this.startY) < 5) {
        if (!this.showmenu) {
          if (x < this.$refs.layout.offsetWidth / 3) {
            // console.log("上一章");
            this.changechapter(this.curentchapterindex - 1);
            return;
          }
          if (x > this.$refs.layout.offsetWidth / 3 * 2) {
            // console.log("下一章");
            this.changechapter(this.curentchapterindex + 1);
            return;
          }
        }
        if (x >= this.$refs.layout.offsetWidth / 3 && x <= this.$refs.layout.offsetWidth / 3 * 2 && y >= this.$refs.layout.offsetHeight / 3 && y <= this.$refs.layout.offsetHeight / 3 * 2) {
          // console.log("菜单");
          this.togglemenu();
          return;
        }
      }
    }
  },
  beforeMount () {
    this.layout_tips_style = this.$store.state.setting.layout_tips_style;
    this.chapter_tips_style = this.$store.state.setting.chapter_tips_style;
    this.title_tips_style = this.$store.state.setting.title_tips_style;
    this.curentchapterindex = this.chapterindex;
    this.getchapters();
  },
  mounted () {
    //触控监听
    this.$refs.layout.addEventListener('touchstart', this.handleTouchStart, false);
    this.$refs.layout.addEventListener('touchend', this.handleTouchEnd, false);
  },
  beforeDestroy () {
    const mybooks = this.$store.state.mybooks.map(book => {
      if (book._id === this.bookid) {
        return { ...book, chapterindex: this.curentchapterindex }
      } else {
        return book;
      }
    })
    const setting = {
      layout_tips_style: this.layout_tips_style,
      chapter_tips_style: this.chapter_tips_style,
      title_tips_style: this.title_tips_style
    }
    this.$store.commit('UPDATE_MYBOOKS', mybooks);
    this.$store.commit('UPDATE_SETTING', setting);
  }
}
</script>
<style scoped>
.layout {
  position: relative;
  width: 100vw;
  height: 100vh;
}
.reader {
  position: relative;
  top: 10px;
  width: 100%;
  height: calc(100% - 20px);
  overflow-y: auto;
}
.chaptertitle {
  width: 100%;
  height: 60px;
  line-height: 60px;
  font-size: 22px;
  font-weight: bold;
}
.chaptercontent {
  width: 100%;
  line-height: 30px;
  text-align: justify;
  white-space: pre-line;
  box-sizing: border-box;
  padding: 0 20px 10px 20px;
  user-select: none;
}
.chapterfooter {
  font-size: 12px;
  color: #ccc;
  padding: 5px;
}
.topmenu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  line-height: 50px;
  height: 50px;
  background-color: #19be6b;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
  z-index: 10;
}
.back {
  position: absolute;
  box-sizing: border-box;
  line-height: 30px;
  padding: 0px 10px 0px 30px;
  background-image: url("../../imgs/back.png");
  background-repeat: no-repeat;
  background-position: 3px 3px;
  background-size: 24px 24px;
  color: #fff;
  top: 10px;
  left: 10px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 20px;
}
.chaptericon {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 10px;
  right: 15px;
}
.footermune {
  box-sizing: border-box;
  padding: 0 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  text-align: left;
  background-color: #19be6b;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
  z-index: 10;
}
.bgcolor,
.fontsize {
  width: 100%;
  height: 25px;
  line-height: 25px;
}
.bgcolor b {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 2px 10px 0 0;
}
.fontsize b {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  text-align: center;
  margin-right: 10px;
  background-color: #fff;
  font-weight: normal;
  padding: 0 2px;
}
.chapterlist {
  position: absolute;
  box-sizing: border-box;
  padding: 10px 20px;
  width: 100%;
  height: calc(100% - 100px);
  top: 50px;
  left: 0;
  overflow-y: auto;
  background-color: #ffebcd;
  text-align: left;
  font-size: 14px;
  z-index: 100;
}
.chapteritem {
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
}
</style>
